@keyframes spinnerRotateAnimation {
  to {
    transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
  }
}
@-webkit-keyframes spinnerRotateAnimation {
  to {
    transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
  }
}
aui-spinner {
  display: block;
  position: relative;
  z-index: 2000000000;
}
aui-spinner[filled] {
  height: 0 !important;
  width: 0 !important;
  margin: auto;
}
aui-spinner[filled][size="small"] > .aui-spinner > svg {
  left: -10px;
}
aui-spinner[filled][size="medium"] > .aui-spinner > svg {
  left: -15px;
}
aui-spinner[filled][size="large"] > .aui-spinner > svg {
  left: -25px;
}
aui-spinner[size="small"] {
  height: 20px;
  width: 20px;
}
aui-spinner[size="small"] .aui-spinner > svg {
  stroke-dasharray: 56px;
  stroke-dashoffset: 45px;
  stroke-width: 2px;
}
aui-spinner[size="medium"] {
  height: 30px;
  width: 30px;
}
aui-spinner[size="medium"] .aui-spinner > svg {
  stroke-dasharray: 84px;
  stroke-dashoffset: 67px;
  stroke-width: 3px;
}
aui-spinner[size="large"] {
  height: 50px;
  width: 50px;
}
aui-spinner[size="large"] .aui-spinner > svg {
  stroke-dasharray: 141px;
  stroke-dashoffset: 113px;
  stroke-width: 5px;
}
aui-spinner .aui-spinner {
  position: relative;
}
aui-spinner .aui-spinner > svg {
  position: absolute;
  animation: 0.86s cubic-bezier(0.4, 0.15, 0.6, 0.85) infinite spinnerRotateAnimation;
  -webkit-animation: 0.86s cubic-bezier(0.4, 0.15, 0.6, 0.85) infinite spinnerRotateAnimation;
  fill: none;
  stroke: #42526E;
  stroke-linecap: round;
  transform-origin: center;
  -ms-transform-origin: center;
  -webkit-transform-origin: center;
}
