/**
 * TYPOGRAPHY - 14px base font size, agnostic font stack
 */
body {
  color: #172B4D;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.42857143;
  letter-spacing: 0;
}
/* International Font Stacks*/
[lang|=en] {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
}
[lang|=ja] {
  font-family: "Hiragino Kaku Gothic Pro", "\30D2\30E9\30AE\30CE\89D2\30B4   Pro W3", "\30E1\30A4\30EA\30AA", Meiryo, "\FF2D\FF33   \FF30\30B4\30B7\30C3\30AF", Verdana, Arial, sans-serif;
}
/* Default margins */
p,
ul,
ol,
dl,
h1,
h2,
h3,
h4,
h5,
h6,
blockquote,
pre,
form.aui,
table.aui,
.aui-tabs,
.aui-panel,
.aui-group {
  margin: 10px 0 0 0;
}
/* No top margin to interfere with box padding */
p:first-child,
ul:first-child,
ol:first-child,
dl:first-child,
h1:first-child,
h2:first-child,
h3:first-child,
h4:first-child,
h5:first-child,
h6:first-child,
blockquote:first-child,
pre:first-child,
form.aui:first-child,
table.aui:first-child,
.aui-tabs:first-child,
.aui-panel:first-child,
.aui-group:first-child {
  margin-top: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  color: #172B4D;
}
/* Headings: desired line height in px / font size = unitless line height */
h1 {
  font-size: 24px;
  font-weight: 500;
  line-height: 1.25;
  letter-spacing: -0.01em;
  text-transform: none;
  margin: 30px 0 0 0;
}
h2 {
  font-size: 20px;
  font-weight: 500;
  line-height: 1.5;
  letter-spacing: -0.008em;
  text-transform: none;
  margin: 30px 0 0 0;
}
h3 {
  font-size: 16px;
  font-weight: 500;
  line-height: 1.25;
  letter-spacing: -0.006em;
  text-transform: none;
  margin: 30px 0 0 0;
}
h4 {
  font-size: 14px;
  font-weight: 600;
  line-height: 1.42857143;
  letter-spacing: -0.003em;
  text-transform: none;
  margin: 20px 0 0 0;
}
h5 {
  font-size: 12px;
  font-weight: 600;
  line-height: 1.66666667;
  letter-spacing: 0;
  text-transform: none;
  margin: 20px 0 0 0;
}
h6 {
  color: #6B778C;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.66666667;
  letter-spacing: 0;
  text-transform: none;
  margin: 20px 0 0 0;
}
h1:first-child,
h2:first-child,
h3:first-child,
h4:first-child,
h5:first-child,
h6:first-child {
  margin-top: 0;
}
/* Nice styles for using subheadings */
h1 + h2,
h2 + h3,
h3 + h4,
h4 + h5,
h5 + h6 {
  margin-top: 10px;
}
/* Increase the margins on all headings when used in the group/item pattern ... */
.aui-group > .aui-item > h1:first-child,
.aui-group > .aui-item > h2:first-child,
.aui-group > .aui-item > h3:first-child,
.aui-group > .aui-item > h4:first-child,
.aui-group > .aui-item > h5:first-child,
.aui-group > .aui-item > h6:first-child {
  margin-top: 20px;
}
/* ... unless they're the first-child */
.aui-group:first-child > .aui-item > h1:first-child,
.aui-group:first-child > .aui-item > h2:first-child,
.aui-group:first-child > .aui-item > h3:first-child,
.aui-group:first-child > .aui-item > h4:first-child,
.aui-group:first-child > .aui-item > h5:first-child,
.aui-group:first-child > .aui-item > h6:first-child {
  margin-top: 0;
}
/* Other typographical elements */
small {
  color: #7A869A;
  font-size: 12px;
  line-height: 1.33333333;
}
code,
kbd {
  font-family: "SFMono-Medium", "SF Mono", "Segoe UI Mono", "Roboto Mono", "Ubuntu Mono", Menlo, Courier, monospace;
}
var,
address,
dfn,
cite {
  font-style: italic;
}
cite:before {
  content: "\2014   \2009";
}
blockquote {
  border-left: 1px solid #DFE1E6;
  color: #7A869A;
  margin-left: 19px;
  padding: 10px 20px;
}
blockquote > cite {
  display: block;
  margin-top: 10px;
}
q {
  color: #7A869A;
}
q:before {
  content: open-quote;
}
q:after {
  content: close-quote;
}
abbr {
  border-bottom: 1px #7A869A dotted;
  cursor: help;
}
