/*! AUI Lozenge */
.aui-lozenge {
  background: #DFE1E6;
  border: 1px solid #DFE1E6;
  border-radius: 3px;
  color: #253858;
  display: inline-block;
  font-size: 11px;
  font-weight: bold;
  line-height: 99%;
  /* cross-browser compromise to make the line-height match the font-size */
  margin: 0;
  padding: 2px 5px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
}
.aui-lozenge.aui-lozenge-subtle {
  background-color: #FFFFFF;
  border-color: #C1C7D0;
  color: #42526E;
}
.aui-lozenge-success {
  background-color: #00875A;
  border-color: #00875A;
  color: #FFFFFF;
}
.aui-lozenge-success.aui-lozenge-subtle {
  background-color: #FFFFFF;
  border-color: #ABF5D1;
  color: #00875A;
}
.aui-lozenge-error {
  background-color: #DE350B;
  border-color: #DE350B;
  color: #FFFFFF;
}
.aui-lozenge-error.aui-lozenge-subtle {
  background-color: #FFFFFF;
  border-color: #FFBDAD;
  color: #DE350B;
}
.aui-lozenge-current {
  background-color: #B3D4FF;
  border-color: #B3D4FF;
  color: #0052CC;
}
.aui-lozenge-current.aui-lozenge-subtle {
  background-color: #FFFFFF;
  border-color: #B3D4FF;
  color: #0052CC;
}
.aui-lozenge-complete,
.aui-lozenge-new {
  background-color: #5243AA;
  border-color: #5243AA;
  color: #FFFFFF;
}
.aui-lozenge-complete.aui-lozenge-subtle,
.aui-lozenge-new.aui-lozenge-subtle {
  background-color: #FFFFFF;
  border-color: #C0B6F2;
  color: #5243AA;
}
.aui-lozenge-moved {
  background-color: #FFAB00;
  border-color: #FFAB00;
  color: #253858;
}
.aui-lozenge-moved.aui-lozenge-subtle {
  background-color: #FFFFFF;
  border-color: #FFF0B3;
  color: #42526E;
}
