aui-message {
  display: block;
  margin: 20px 0 0 0;
}
.aui-message {
  background: #FFFFFF;
  border: 1px solid #0052CC;
  border-radius: 3px;
  color: #172B4D;
  line-height: 20px;
  margin: 20px 0 0 0;
  overflow-wrap: break-word;
  padding-bottom: 20px;
  padding-left: 50px;
  padding-right: 20px;
  padding-top: 20px;
  position: relative;
  word-wrap: break-word;
  word-break: normal;
  /** deprecated; replaced with icon font */
}
.aui-message::after {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0;
  font-family: "Adgs Icons";
  font-weight: normal;
  font-style: normal;
  speak: none;
  color: #FFFFFF;
  font-size: 16px;
  left: 20px;
  line-height: 20px;
  position: absolute;
  top: 20px;
}
.aui-message::before {
  display: none;
}
.aui-message::after {
  content: "\F234";
  color: #0052CC;
}
.aui-message .aui-icon.icon-close {
  background-image: none;
  color: #505F79;
  text-indent: inherit;
}
.aui-message .aui-icon.icon-close::before {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0;
  font-family: "Adgs Icons";
  font-weight: normal;
  font-style: normal;
  speak: none;
  content: "\F11B";
  font-size: 16px;
}
.aui-message .aui-icon.icon-close:hover {
  color: #172B4D;
}
.aui-message .aui-icon-success,
.aui-message .aui-icon-warning,
.aui-message .aui-icon-info,
.aui-message .aui-icon-error,
.aui-message .aui-icon-hint,
.aui-message .aui-icon-generic,
.aui-message .icon-generic,
.aui-message .icon-error,
.aui-message .icon-hint,
.aui-message .icon-info,
.aui-message .icon-success,
.aui-message .icon-warning {
  display: none;
}
aui-message:first-child,
.aui-message:first-child {
  margin-top: 0;
}
.aui-popup .aui-message.closeable,
.aui-dialog .aui-message.closeable {
  z-index: 4000;
}
/* DEPRECATED .error, .warning, .hint, .info, .success */
.aui-message.error,
.aui-message-error {
  background: #FFFFFF;
  border-color: #FF5630;
  color: #172B4D;
}
.aui-message.error::after,
.aui-message-error::after {
  content: "\F15A";
  color: #FF5630;
}
.aui-message.warning,
.aui-message-warning {
  background: #FFFFFF;
  border-color: #FFAB00;
  color: #172B4D;
}
.aui-message.warning::after,
.aui-message-warning::after {
  content: "\F1B3";
  color: #FFAB00;
}
.aui-message.hint,
.aui-message-hint,
.aui-message.info,
.aui-message-info {
  background: #FFFFFF;
  border-color: #0052CC;
  color: #172B4D;
}
.aui-message.hint::after,
.aui-message-hint::after,
.aui-message.info::after,
.aui-message-info::after {
  content: "\F234";
  color: #0052CC;
}
.aui-message.success,
.aui-message-success {
  background: #FFFFFF;
  border-color: #36B37E;
  color: #172B4D;
}
.aui-message.success::after,
.aui-message-success::after {
  content: "\F104";
  color: #36B37E;
}
/* Message titles */
.aui-message p.title {
  font-weight: bold;
}
/* IE double bolds otherwise... */
.aui-message p.title strong {
  font-weight: inherit;
}
.aui-message.closeable {
  padding-right: 50px;
}
.aui-message.closeable .icon-close {
  cursor: pointer;
  left: auto;
  opacity: 0;
  position: absolute;
  right: 20px;
  top: 20px;
}
.aui-message.closeable:hover .icon-close,
.aui-message.closeable .icon-close:focus,
.aui-message.closeable:not(.fadeout):not(.aui-will-close) .icon-close {
  opacity: 1;
}
/* End aui-icon position */
/* Footer interop (provisional, just enough to stop them looking broken) */
#footer .aui-message {
  margin-left: 20px;
  margin-right: 20px;
  text-align: left;
}
