#footer-logo {
  margin-top: 20px;
}
#footer-logo a {
  background: url(./images/atlassian-horizontal-neutral.svg) center center no-repeat;
  background-size: 127px 24px;
  display: block;
  height: 24px;
  margin: 0 auto;
  overflow: hidden;
  text-align: left;
  text-indent: -9999em;
  width: 127px;
}
#footer-logo a:focus,
#footer-logo a:hover,
#footer-logo a:active {
  background-image: url(./images/atlassian-horizontal-blue.svg);
}
