.aui-layer {
  position: fixed;
}
.aui-layer[aria-hidden="true"] {
  display: none;
}
.aui-blanket {
  opacity: 0;
  transition: opacity 0.2s, visibility 0.2s;
  transition-delay: 0.1s;
  visibility: hidden;
  background: rgba(23, 43, 77, 0.45);
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 2500;
}
.aui-blanket[aria-hidden="false"] {
  opacity: 1;
  transition: opacity 0.2s;
  transition-delay: 0.1s;
  visibility: visible;
}
