/* Dropdown icon (downwards arrow)
* - achieved via pseudo element
*/
.aui-icon-dropdown {
  border: 4px solid transparent;
  border-top-color: #172B4D;
  content: "";
  display: inline-block;
  height: 0;
  overflow: hidden;
  text-align: left;
  text-indent: -999em;
  position: relative;
  vertical-align: baseline;
  width: 0;
}
